import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "@fortawesome/fontawesome-free/css/all.css";
import Navbar from "./components/Navbar";
import Competencias from "./components/Competencias";
import GraficoTecnologias from "./components/GraficoTecnologias";
import Sintese from "./components/Sintese";
import Experiencias from "./components/Experiencias";
import Footer from "./components/Footer";
import { motion } from "framer-motion";

function App() {
  const [darkMode, setDarkMode] = useState(
    () => localStorage.getItem("theme") === "dark"
  );

  useEffect(() => {
    document.body.className = darkMode
      ? "bg-dark text-light"
      : "bg-light text-dark";
    localStorage.setItem("theme", darkMode ? "dark" : "light");
  }, [darkMode]);

  const cardClass = `shadow-sm p-4 mb-4 rounded ${
    darkMode
      ? "bg-dark bg-opacity-75 border border-light text-light"
      : "bg-white bg-opacity-75"
  }`;

  const scrollTo = (id) => {
    document.getElementById(id).scrollIntoView({ behavior: "smooth" });
  };

  return (
    <>
      <nav
        className={`navbar navbar-expand-lg fixed-top ${
          darkMode ? "navbar-dark bg-primary" : "navbar-light bg-light"
        } border-bottom`}
      >
        <a className="navbar-brand" href="#">
          RAFAEL H. JOVANELI
        </a>
        <div className="ml-auto">
          <button
            className="btn btn-link text-light"
            onClick={() => scrollTo("competencias")}
          >
            Competências
          </button>
          <button
            className="btn btn-link text-light"
            onClick={() => scrollTo("grafico")}
          >
            Tecnologias
          </button>
          <button
            className="btn btn-link text-light"
            onClick={() => scrollTo("experiencias")}
          >
            Experiências
          </button>
          <button
            className={`btn ${
              darkMode ? "btn-outline-light" : "btn-outline-dark"
            } ml-2`}
            onClick={() => setDarkMode(!darkMode)}
          >
            {darkMode ? "Light" : "Dark"}
          </button>
        </div>
      </nav>

      <div className="container py-5" style={{ marginTop: "70px" }}>
        <div className={cardClass}>
          <Competencias />
        </div>
        <div className={cardClass}>
          <GraficoTecnologias />
        </div>
        <div className={cardClass}>
          <Sintese />
        </div>
        <div className={cardClass}>
          <Experiencias />
        </div>
        <Footer />
      </div>
    </>
  );
}

export default App;
