import React from "react";
import { motion } from "framer-motion";

function Sintese() {
  return (
    <motion.section
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8 }}
      viewport={{ once: true }}
    >
      <h3>SÍNTESE PROFISSIONAL</h3>
      <p>
        Experiência em JAVA voltada ao cliente Bradesco (Ambiente AWB) e Nova
        Arquitetura de Sistemas (NAS).
      </p>
      <p>
        Utilização de Gradle, Maven, Spring Boot, JSF PrimeFaces, JavaScript,
        JQuery, HTML, XML, JUnit, GitHub, Bitbucket, GitLab, SVN, STAR-TEAM, SQL
        Server, PL/SQL, SOAP, REST, AWS, Docker, Netbeans, Eclipse, IBM RAD,
        Tomcat, JBoss, Weblogic, Websphere, VRaptor, IntelliJ.
      </p>
      <p>Vivência com atendimento internacional e ITIL V3.</p>
    </motion.section>
  );
}

export default Sintese;
