import React, { useEffect } from "react";
import Chart from "chart.js/auto";
import { motion } from "framer-motion";

function GraficoTecnologias() {
  useEffect(() => {
    const ctx = document.getElementById("techChart");
    new Chart(ctx, {
      type: "bar",
      data: {
        labels: ["Java", "Spring Boot", "SQL", "Docker", "AWS", "Git"],
        datasets: [
          {
            data: [95, 90, 85, 80, 75, 85],
            backgroundColor: "rgba(54, 162, 235, 0.7)",
            borderRadius: 5,
            barThickness: 20,
          },
        ],
      },
      options: {
        indexAxis: "y",
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          x: {
            min: 0,
            max: 100,
            grid: { display: false },
            title: { display: true, text: "Porcentagem (%)" },
          },
          y: { grid: { display: false } },
        },
        plugins: { legend: { display: false } },
      },
    });
  }, []);

  return (
    <motion.section
      id="grafico"
      style={{ height: "300px" }}
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8 }}
      viewport={{ once: true }}
    >
      <h3>Nível de Conhecimento em Tecnologias</h3>
      <canvas id="techChart"></canvas>
    </motion.section>
  );
}

export default GraficoTecnologias;
