import React from "react";
import { motion } from "framer-motion";

function Competencias() {
  return (
    <motion.section
      id="competencias"
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8 }}
      viewport={{ once: true }}
    >
      <h3>
        COMPETÊNCIAS <i className="fas fa-clipboard-list"></i>
      </h3>
      <ul>
        <li>
          <i className="fas fa-language mr-2"></i> Fluência em inglês. Espanhol
          Básico. Francês Básico.
        </li>
        <li>
          <i className="fab fa-java mr-2"></i> Experiência em JAVA versões 6, 7,
          8, 11 e 17.
        </li>
        <li>
          <i className="fas fa-cogs mr-2"></i> Vivência em programação reativa
          (Spring Webflux).
        </li>
        <li>
          <i className="fas fa-tasks mr-2"></i> Conhecimentos sólidos em
          metodologias Ágeis.
        </li>
        <li>
          <i className="fas fa-user-check mr-2"></i> Proativo, organizado,
          dinâmico, comprometido e com ótima habilidade de comunicação.
        </li>
        <li>
          <i className="fas fa-users-cog mr-2"></i> Experiência com coordenação
          e liderança de equipes.
        </li>
      </ul>
    </motion.section>
  );
}

export default Competencias;
